import ClockIcon from 'material-ui/svg-icons/device/access-time';
import React from 'react';
import moment from 'moment';
import Paper from 'material-ui/Paper';

import merge from '../utils/merge.js';
import {FlexBox, FlexBoxJustify} from './FlexBox.jsx';
import FlatButton from 'material-ui/FlatButton';

const styles = {
  wrapper: {
    borderLeftStyle: 'solid',
    borderLeftWidth: 4,
    marginBottom: 10,
  },

  eventTitle: {
    fontFamily: 'Roboto Condensed',
    fontSize: 18,
    fontWeight: 'bold',
    lineHeight: '21px',
    color: '#2B2D34',
  },

  eventType: {
    fontSize: 14,
    lineHeight: '17px',
    color: '#2B2D34',
  },

  scheduleInfoLine1: {
    fontFamily: 'Roboto Condensed',
    fontSize: 18,
    fontWeight: 'bold',
    lineHeight: '21px',
    color: '#2B2D34',
    whiteSpace: 'nowrap',
  },

  scheduleInfoLine2: {
    fontSize: 14,
    lineHeight: '17px',
    color: '#6D6C6C',
    textTransform: 'uppercase',
    whiteSpace: 'nowrap',
  },

  activeBlue: '#009FDD',
};

const isOpenBooking = (customerEvent) => (
  customerEvent.schedules[0].duration != null
);

const dateRange = (start, end, format) => {
  return `${moment(start).format(format)} - ${moment(end).format(format)}`;
};

const humanizeDuration = (dur, unit = 'seconds') => {
  let duration;
  if (moment.isDuration(dur)) {
    duration = dur;
  } else {
    duration = moment.duration(dur, unit);
  }

  const hours = Math.floor(duration.asHours());
  const minutes = duration.minutes();
  const hoursText = !!hours ? `${hours}h` : ''; // eslint-disable-line no-extra-boolean-cast
  const minutesText = !!minutes ? `${minutes}m` : ''; // eslint-disable-line no-extra-boolean-cast

  return `${hoursText} ${minutesText}`.trim();
}

const url = (customerEvent) => (
  "https://app.upperhand.io"
);

const BlueButton = ({label}) => (
  <FlatButton
    backgroundColor={styles.activeBlue}
    hoverColor={styles.activeBlue}
    primary
    disableTouchRipple
    style={{color: 'white'}}
    label={label}
  />
);

const CardLayout = ({
  athletes,
  automations,
  event,
  scheduleInfoLine1,
  scheduleInfoLine2,
}) => (
  <Paper
    style={
      merge(styles.wrapper, {
        borderLeftColor: event.event_type && event.event_type.color,
        padding: '6px 16px 6px 18px',
      })
    }
  >
    <a href={event.url} style={{textDecoration: 'none'}} target="_blank" rel="noreferrer">
      <FlexBoxJustify style={{flexWrap: 'wrap'}}>
        <div style={{flex: '1 1 60%', margin: '6px 0'}}>
          <div style={styles.eventTitle}>{event.title}</div>
          <div style={styles.eventType}>{event.event_type_name}</div>
        </div>
        <FlexBoxJustify style={{flex: '1 1 40%', alignItems: 'center', margin: '6px 0'}}>
          <div>
            <div style={styles.scheduleInfoLine1}>{scheduleInfoLine1}</div>
            <div style={styles.scheduleInfoLine2}>{scheduleInfoLine2}</div>
          </div>
          <BlueButton
            label={event.price}
          />
        </FlexBoxJustify>
      </FlexBoxJustify>
    </a>
  </Paper>
);


const FixedScheduleCard = ({customerEvent, ...props}) => {
  const startDate = moment(customerEvent.schedules[0].availability_schedule.start_date);
  const endDate = moment(customerEvent.schedules[0].availability_schedule.end_date);

  const dateRangeText = startDate === endDate ?
    startDate.format('MMM D') :
    dateRange(startDate, endDate, 'MMM D');

  return (
    <CardLayout
      {...props}
      event={customerEvent}
      scheduleInfoLine1={dateRangeText}
      scheduleInfoLine2={startDate.format('ddd')}
    />
  );
};

const OpenBookingCard = ({customerEvent, ...props}) => (
  <CardLayout
    {...props}
    event={customerEvent}
    scheduleInfoLine1={
      <FlexBox style={{alignItems: 'baseline'}}>
        <ClockIcon style={{height: 16, width: 16, marginRight: 2}} />
        <div>{humanizeDuration(customerEvent.schedules[0].duration)}</div>
      </FlexBox>
    }
    scheduleInfoLine2="Session"
  />
);

const eventCard = ({customerEvent}) => {
  return (<div>
    {
      isOpenBooking(customerEvent) ?
        <OpenBookingCard customerEvent={customerEvent} /> :
        <FixedScheduleCard customerEvent={customerEvent} />
    }
  </div>)
};

export default eventCard;
