import $ from 'jquery';
import Cookies from 'js-cookie';
import React from 'react';
import EventsActions from '../actions/EventsActions.js';
import EventCard from './EventCard.jsx';

const PaginatedCardList = ({events}) => {
  return (
    <div>
      {events.map( (e) => (
        <EventCard
          customerEvent={e}
        />
      ))}
    </div>
  )
}

class EventList extends React.Component {
  componentDidMount() {
    $.ajax(`https://app.upperhand.io/users/jwt?customer_id=${this.props.customerId}`, {
      success: (r) => {
        Cookies.set('uh_token', r['token']);
        $.ajax({
         url: this.url('events'),
         type: "GET",
         beforeSend: (xhr) => {
           xhr.setRequestHeader('X-Jwt-Token', Cookies.get('uh_token'));
         },
         success: (r) => { EventsActions.listEvents(r['events']); }
         });
        }
      });
  }

  url(path) {
    return `https://api.upperhand.io/api/${path}`;
  }

  render() {
    return(
      <PaginatedCardList events={this.props.eventsStore.events} />
    );
  }
}

export default EventList;
