import alt from '../alt.js';
import EventsActions from '../actions/EventsActions.js';


class EventStore {
  constructor() {
    this.events = [];
    this.bindListeners({
      handleListEvents: EventsActions.listEvents
    });
  }

  handleListEvents(events) {
    this.events = events;
  }
}

export default alt.createStore(EventStore, 'EventStore');
