import AltContainer from 'alt-container';
import React from 'react';
import ReactDOM from 'react-dom';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import changeBackgroundColor from './utils/background.js';
import EventList from './components/EventList.jsx';
import EventsStore from './stores/EventsStore.js';

class Widget extends React.Component {
  render() {
    return (
      <MuiThemeProvider>
        <AltContainer
          stores={{
            eventsStore: EventsStore,
          }}
        >
        <EventList customerId={this.props.customerId} />
      </AltContainer>
      </MuiThemeProvider>
    );
  }
}

changeBackgroundColor();
const dom = document.getElementById('root')
ReactDOM.render(<Widget customerId={dom.dataset.customer_id} />, dom);
